import './style/coin_tracker.scss'
import ico_clock from './img/ico_clock.png'
import ico_pro from './img/ico_pro.png'

const CoinTracker = () => {
  return(
    <section id='coin_tracker'>
      <div className="color_box">
        <div className='title'>
          <h2>Coin Tracker
          <a href='https://github.com/m1n720n9/cryptocurrency_tracker' target='_blank'><img className='skill_logo' src={require('./img/logo_github.png')} /></a>
          </h2>
        </div>
        <div className='text'>
          <ul>
            <li>React.js     </li>
            <li>React Query  Crypto Price API fetching</li>
            <li>Styled Components </li>
            <li>Apex Charts      </li>
          </ul>
          <p><img src={ico_pro}/>   : React TypeScript</p>
          <p><img src={ico_clock}/>   : 1</p>
          <div className='skills'>
            <img className='skill_logo' src={require("./img/logo_react.png")} />
            <img className='skill_logo' src={require('./img/logo_ts.png')} />
          </div>
        </div>      
      </div>
      <div className="flow_img">
        <a href="https://m1n720n9.github.io/cryptocurrency_tracker/" target='_blank'><img src={require('./img/todo_flow.png')} /></a>
        <span>CLICK TO GO TO PAGE &rarr;</span>
      </div>
    </section>
  )
}

export default CoinTracker;